body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}


.header{
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    background-color: #f8f9fa;
}

.header-content{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-around;
    padding-bottom:5px;
}

.header-name p{
    cursor: pointer;
    font-size: 20px;
    font-weight: 600;
}

.header-list{
    display: flex;
}

.header-list ul{
    display: flex;
    list-style: none;
}


.header-list ul li{
    margin-left:30px;
    cursor: pointer;
    font-size:18px;
    color: gray;
    font-weight: 600;
}

.header-list ul li:hover{
    color:black;
}

.active{
    color: black;
}
.Home{
    background-image: url(https://ifh.cc/g/N5yJs.jpg);
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100vh;
    display: block;
}

h1{
  color:white;
  display: flex;
  margin:0;
  justify-content: center;
  align-items: center;
  height:100%;
  font-size:48px;
}

.text{
  border:1px solid white;
}

.Skill{
    background-color: #e5e5e5;
    background-attachment: fixed;
    background-repeat: no-repeat;
    background-size: cover;
    height: 100%;
    display: block;
    text-align: center;
    padding-bottom: 50px;
}

.Skill-header{
    margin-top:0;
    padding-top: 50px;
    font-size: 32px;
    padding-bottom:30px;
    font-family: 'Fredoka One', cursive;
}

.Skill-image{
    display: flex;
}

.Skill-image-flex{
   display: flex;
   margin-right:auto;
   margin-left:auto;
}

.Skill-images{
    width: 100px;
    height: 100px;
    margin-left: 20px;
    border-radius: 30px 30px;
    box-shadow: 0 3px 6px rgba(0,0,0,.2), 0 3px 6px rgba(0,0,0,.2);
    background-size: cover;
    background-size: cover;
}

.Shoes{
    height:100%;
    border-bottom: 1px solid #e5e5e5;
    padding-bottom:7%;
}
.Shoes-header{
    padding-top: 7%;
}

.Shoes-header h2{
    text-align: center;
    font-family: 'Fredoka One', cursive;
    font-size: 38px;
}

.Shoes-header p{
    text-align: center;
    font-size: 16px;
}

.Shoes-image{
    margin-right:30px;
}

.Shoes-image img{
    width: 500px;
    height: 350px;
}

.Shoes-body{
    margin-top:5%;
    margin-left:20%;
}

.row{
    display: flex;
    flex-wrap: wrap;
}

